  .showDiv {
    background: #03A9F4;
    color: white;
    padding: 10px 20px;
    display: inline-block;
    cursor: pointer;
  }

  .square {
    background: rgb(253, 253, 253);
    /* height: 100px; */
    opacity: 1;
    transition: all 2s ease;
    width: 93%;
    margin: 5% auto;
    border-radius: 2%;
    padding: 3%;
    font-family: 'Roboto';
    box-shadow: 0vw 0vw 2vw;
  }
  
  .square.false {
    opacity: 0;
    background: #E66425;
    -webkit-transform: translateX(-110%);
            transform: translateX(-110%);
  }
  
  .square.true {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  
  .square-enter.square-enter-active {
    background: #E66425;
  }
  
  .square-exit.square-exit-active {
    background: rgb(115, 67, 25);
  }


  @media screen and (min-width: 800px) {
    .square {
      width: 63%;
    }
    .square.true {
      -webkit-transform: translateX(0%);
      transform: translateX(20%);
  }

  }

  
  
  @media screen and (min-width: 1200px) {
    .square {
      background: rgb(253, 253, 253);
      height: 20vw;
      opacity: 1;
      transition: all 2s ease;
      width: 79%;
      margin: 1% auto;
      border-radius: 1%;
      padding: 3%;
      font-family: 'Roboto';
    }
    .square.true {
      -webkit-transform: translateX(0%);
      transform: translateX(10%);
  }

  }


.customer-sort{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.customer-type,.button{
    width: 50%;
}
.button button{
    margin: 10% 0 0 10%;
    background-color: #E66425;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    font-size: 2.2vw;
    box-shadow: blue;
    border-radius: 4%;
    width: 80%;
    border: 1px solid #E66425;
    padding: 4%;
    box-shadow: 0vw 0vw 5vw 1vw #ccc;
}
.type{
    margin: 10% 0 0 10%;
    font-size: 2.2vw;
    padding: 2vw;
    width: 20vw;
    box-shadow: 0vw 0vw 4vw 0vw #ccc;
}



@media screen and (min-width: 800px) {
    
    .type {
        margin: 10% 0 0 10%;
        font-size: 1.8vw;
        padding: 0.5vw;
        width: 9vw;
        box-shadow: 0vw 0vw 4vw 0vw #ccc;
    }
    .button button {
        margin: 10% 0 0 50%;
        font-size: 1.6vw;
        border-radius: 3%;
        width: 50%;
        padding: 3%;
        box-shadow: 0vw 0vw 1vw 0.3vw #ccc;
    }
    .customer-sort {
        display: flex;
        flex-wrap: wrap;
        width: 73%;
        margin-left: 25%;
        /* float: left; */
    }
}



@media screen and (min-width: 1200px) {
    .type {
        font-size: 0.7vw;
        width: 7vw;
        box-shadow: 0vw 0vw 1vw 0vw #ccc;
    }
    .button button {
        margin: 10% 0 0 50%;
        font-size: 0.8vw;
        border-radius: 3%;
        width: 46%;
        padding: 2%;
        box-shadow: 0vw 0vw 1vw 0.3vw #ccc;
    }
    .customer-sort {
        display: flex;
        flex-wrap: wrap;
        width: 83%;
        margin-left: 16%;
        /* float: left; */
    }
    
}

body{
    margin: 0;
    padding: 0;
    /* background: #ececece; */
    background: #e8e4e4;
}
#root{
    background: #f4f3f3;
}
a {
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
}
.header{
    background: #fdfdfd;
    box-shadow: 0vw 0vw 5vw 0vw #ccc;
}
.header-options{
    display: flex;
}
.main-logo{
    flex-grow: 1;
    padding: 2%;
}
.main-logo img{
    width: 77%;
   
}
.nav-links{
    margin: auto;
    width: 200%;
}
.user-pic-holder {
    display: none;
}

.nav-links a{
    text-decoration: none;
    color: black;
    font-size: 3vw;
    margin-left: 25%;
}



@media only screen and (min-width: 800px) {
    .bars {
        display: none;
    }
    .header {
        box-shadow: 0vw 0vw 5vw 0vw #ccc;
        width: 79%;
        height: 11vw;
        float: right;
    }
    .user-pic-holder {
        display: block;
        background-image: url(data:image/png;base64,);
        background-size: contain;
        border-radius: 50%;
        margin-left: 55%;
        background-repeat: no-repeat;
        width: 10%;
    }
    .main-logo, .main-logo img {
       
        display: none;
    }
    .header-options {
        /* width: 7%; */
        display: flex;
        /* border-top: 1.25px solid black; */
        /* height: 8.5vw; */
    }
    .nav-links a {
        font-size: 1.4vw;
    }
    .nav-links {
        padding: 2% 0 0 0%;
        width: 7%;
        margin: 4% 0 3% 9%;
    }
    
}



@media only screen and (min-width: 1200px) {
    body {
        margin: 0 10%;
    }
    .header {
        box-shadow: 0vw 0vw 5vw 0vw #ccc;
        width: 87%;
        float: right;
        height: 6vw;
    }
    .nav-links a {
        text-decoration: none;
        color: black;
        font-size: 0.8vw;
        margin-left: 0%;
    }
    .nav-links {
        /* padding: 0% 0 0 0%; */
        width: 0%;
        margin: 2% 0 3% 5%;
    }
    .user-pic-holder {
        background-image: url(data:image/png;base64,);
        background-size: contain;
        border-radius: 50%;
        margin-left: 80%;
        background-repeat: no-repeat;
        width: 7%;
    }

}

